.banner {
  height: 320px;
  background: #77C6FF;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  div {
    flex: auto;
    background-repeat: repeat-x;
    background-size: auto 100%;
  }
  .banner-l {
    background-position: 100% 0;
  }
  .banner-r {
    background-position: 0 0;
    margin-left: -1px;
  }
}
.title {
  text-align: center;
  h1 {
    margin-bottom: 20px;
    font-weight: 900;
    font-size: 68px;
  }
  span {
    display: block;
    font-size: 16px;
    line-height: 1.5;
    margin-bottom: 40px;
    padding: 0;
  }
  .btn-box {
    text-align: center;
  }
}
.content {
  width: 1200px;
  margin: 0 auto;
  .slogan {
    height: auto;
    margin: 0 auto;
    padding-top: 43px;
    text-align: center;
    h1 {
      font-size: 34px;
      color: rgba(0,0,0,.88);
      margin-bottom: 10px;
    }
    span {
      font-size: 18px;
      color: #666;
    }
  }
  .course-list {
    width: 1068px;
    height: auto;
    margin: 0 auto 20px;
    padding-top: 23px;
    display: flex;
    text-align: center;
    li {
      flex: 1;
      -webkit-transition: .4s;
      padding: 26px 0;
      cursor: default;
      img {
        margin: 0 auto 12px;
      }
      span{
        font-size: 16px;
        color: #333;
        display: block;
        -webkit-transition: .4s;
      }
      &:hover {
        background-color: #f5f8fa;
        -webkit-transition: .4s;
        span{
          color: #f54332;
          -webkit-transition: .4s;
        }
      }
    }
  }
  .online-list {
    box-shadow: 0 0px 18px rgb(0 0 0 / 8%);
    border-radius: 8px;
    border: 1px #fff solid;
    background-color: #f9fbfc;
    margin-top: 38px;
    display: flex;
    text-align: center;
    li {
      flex: 1;
      padding: 26px 0;
      cursor: default;
      span {
        display: block;
        margin: 12px auto 4px;
        font-size: 16px;
        color: #333;
        -webkit-transition: .4s;
      }
      i {
        display: block;
        font-style: normal;
        font-size: 12px;
        color: #666;
        -webkit-transition: .4s;
      }
      &:hover {
        span, i {
          color: #f54332;
          -webkit-transition: .4s;
        }
      }
    }
  }
  .btn-box {
    text-align: center;
    padding: 10px 0 30px;
  }
}